.header {
  width: 100%;
  background-color: transparent;
  padding-block-start: 3.125rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
}
.header--sticky {
  background-color: var(--background-light);
  padding-block: 1.5rem;
  transform: initial;
  transition: background var(--transition-duration) var(--transition-type), transform var(--transition-duration) var(--transition-type);
}
.header__logo {
  height: 1.375rem;
  display: block;
}
.header__logo .image {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
}
.header__top-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.25rem;
}
.header__top-list {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header__top-list-link {
  --_font-size: 1.125rem;
}
.header__nav {
  margin-inline-end: auto;
}
.header__bottom {
  margin-block-start: 1.25rem;
}
.header__bottom-nav {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  border: 1px solid var(--primary-color);
  border-radius: var(--radius-xl);
}
.header__bottom-list {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 0.85rem 1rem 0.85rem 2.5rem;
}
.header__bottom-link {
  --_font-size: 1.25rem;
}
.header__search {
  flex-shrink: 0;
  position: relative;
  z-index: 1;
}
.header__search-btn {
  --_height: 100%;
  --_font-size: 1.125rem;
  --_background-color: var(--primary-color--50);
  --_border-radius: 0 var(--radius-xl) var(--radius-xl) 0;
  --_padding-x: 1.25rem;
  --_icon-size: 1.5rem;
  --_icon-color: var(--primary-color);
  --_border-color-active: var(--primary-color);
  --_background-color-active: var(--primary-color--50);
  --_icon-color-active: var(--primary-color);
  border-inline-start: 1px solid var(--primary-color);
  padding-inline-end: 3rem;
}
.header__search-btn .icon {
  transition: color var(--_transition-duration) var(--_transition-type);
}
.header__search-form {
  width: 32rem;
  height: 3.625rem;
  background-color: var(--primary-color--50);
  border-radius: var(--radius-xl) 0 0 var(--radius-xl);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%);
  position: absolute;
  top: 50%;
  right: 0;
  z-index: -1;
  transition: opacity var(--transition-duration) var(--transition-type), visibility var(--transition-duration) var(--transition-type), right var(--transition-duration) var(--transition-type);
}
.header__search-form.is-active {
  opacity: 1;
  visibility: visible;
  right: 100%;
}
.header__search-input {
  --input-background: var(--primary-color--50);
  --input-border: 1px solid var(--primary-color);
  height: 100%;
  font-size: 1.25rem;
  color: var(--dark-color);
  border-radius: var(--input-radius) 0 0 var(--input-radius);
}
.header__search-input::placeholder {
  font-size: inherit;
  color: var(--dark-color);
}
.header__menu {
  display: none;
}

.burger {
  --burger-width: 1.75rem;
  --burger-height: .875rem;
  --burger-line-height: 2px;
  --burger-line-radius: var(--radius-xxl);
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  border-radius: var(--burger-line-radius);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 80%;
  height: var(--burger-line-height);
  background-color: currentColor;
  border-radius: var(--burger-line-radius);
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

[data-menu-overlay] {
  width: 100%;
  height: 100vh;
  height: 100dvh;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  position: fixed;
  inset: 0;
  z-index: -1;
  transition: opacity var(--transition-duration) var(--transition-type), visibility var(--transition-duration) var(--transition-type);
}

[data-menu-overlay=true] {
  opacity: 1;
  visibility: visible;
  z-index: 15;
}

.banner-main {
  background-image: var(--banner-image);
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.banner-main--blob {
  background-image: initial;
}
.banner-main, .banner-main__decoration, .banner-main__inner {
  background-repeat: no-repeat;
  background-size: 120rem 36.25rem;
  background-position: center 0;
}
.banner-main__inner {
  background-image: var(--banner-inner-image);
  position: relative;
  z-index: 2;
